              description:
                "San Lorenzo de El Escorial, about 45 kilometers northwest of Madrid, was the summer home of Spain's kings, and in 1563, work was begun here on a huge complex, which would include a monastery, church, royal palace, mausoleum, library, and museum, all conceived as a monument to Philip II and his reign. The result is a staggering collection of attractions, built around 16 courtyards, its rooms and structures connected by 16 kilometers of corridors. At its core is the church, the highlight of which is Herrera's 30-meter-high retablo, made of jasper and red marble and approached by a flight of 17 steps. Along with the vaulted and frescoed ceilings by Tibaldi in the rooms off the lower cloister, highlights of the monastery are the Panten de los Reyes (the Baroque burial vault of the Spanish kings) and the library, a grand room also decorated by Tibaldi frescoes. In the palace, be sure to see the Bourbon Suite, where the state apartments of Charles IV are decorated with rare furnishings and 338 tapestries. Beyond are the art-filled private apartments of Philip II. The Picture Gallery below has a large collection of fine paintings, including works by Hieronymus Bosch, Albrecht Drer, Titian, Tintoretto, Veronese, Velzquez, and El Greco.",
